import React from "react";
import img_Me from "../image/img-me.webp";

export default function About() {
  return (
    <div
      id="about"
      className="bg-white p-20 h-auto w-auto flex flex-col items-center justify-center"
    >
      <h1 className="text-4xl text-black text-center font-Comfortaa font-bold">About Me</h1>
      <img
        className="w-32 h-32 rounded-full mx-auto mt-10"
        src={img_Me}
        alt="Me"
      />
      <p className="text-black md:px-56 mt-10 mb-10 text-lg text-justify font-sans font-Nunito">
        I am a <b>Software Developer</b> with advanced knowledge in Artificial Intelligence and I try to be
        up to date on the latest advancements. I mainly work on <b>web
        applications</b> and follow new technologies very closely, I love
        graphic design but I just doodle. In my spare time, I try to create
        video game concepts/prototypes
      </p>
    </div>
  );
}
